import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Brightness1Icon from '@material-ui/icons/Brightness1';
import * as skin from '../../skin';
import { State as StoreState, BootstrapState } from '../../store/reducer';
import Messaging from './Messaging';

export default function Footer() {
  const [chatDisplay, setChatDisplay] = useState(false);
  const [envDisplay, setEnvDisplay] = useState(false);
  const {
    isAppReady,
    isDisconnected,
    network,
    slotInfo,
    hasMember,
  } = useSelector((state: StoreState) => {
    return {
      network: state.common.network,
      slotInfo: state.solana.slotInfo,
      isAppReady:
        state.common.isWalletConnected &&
        state.common.bootstrapState === BootstrapState.Bootstrapped,
      isDisconnected: !state.common.isWalletConnected,
      hasMember: state.registry.member,
    };
  });
  return (
    <div
      style={{
        zIndex: 99,
        position: 'fixed',
        width: '100%',
        bottom: 0,
        textAlign: 'center',
        height: '30px',
        backgroundColor: '#fbfbfb',
        borderTop: 'solid 1pt #ccc',
        display: 'flex',
        justifyContent: 'space-between',
        paddingLeft: '10px',
        paddingRight: '10px',
      }}
    >
      <div
        style={{
          display: 'flex',
        }}
      >
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
            marginRight: '10px',
          }}
        >
          <Brightness1Icon
            style={{
              color: isAppReady
                ? skin.instance().active
                : isDisconnected
                ? '#ccc'
                : skin.instance().ready,
              fontSize: '12px',
            }}
          />
        </div>
        <IconButton
          style={{
            color: 'inherit',
            padding: 0,
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
          }}
          onClick={() => {
            setEnvDisplay(!envDisplay);
          }}
        >
          <Typography style={{ fontSize: '14px', fontWeight: 'bold' }}>
            {network.label}
          </Typography>
        </IconButton>
        {isAppReady && (
          <div
            style={{
              marginLeft: '10px',
              display: 'flex',
              justifyContent: 'center',
              flexDirection: 'column',
            }}
          >
            <Typography
              style={{
                borderLeft: 'solid 1pt #ccc',
                paddingLeft: '10px',
                fontSize: '14px',
                fontWeight: 'bold',
              }}
            >
              Slot {slotInfo?.slot.toString()}
            </Typography>
          </div>
        )}
      </div>
      <div style={{ display: 'flex' }}>
        {hasMember && isAppReady && (
          <div
            style={{ display: 'flex' }}
            onClick={() => {
              setChatDisplay(!chatDisplay);
            }}
          >
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                flexDirection: 'column',
              }}
            >
              <Button style={{ padding: 0 }}>
                <Typography style={{ fontSize: '14px', fontWeight: 'bold' }}>
                  Chat
                </Typography>
              </Button>
            </div>
          </div>
        )}
      </div>
      {envDisplay && (
        <div
          style={{
            overflowY: 'scroll',
            position: 'fixed',
            bottom: '30px',
            left: 0,
            width: '500px',
            height: '400px',
            borderTopLeftRadius: '4px',
            boxShadow: 'rgba(0, 0, 0, 0.05) 0px 0px 25px 0px',
            background: '#fff',
          }}
        >
          <NetworkEnvironment />
        </div>
      )}
      {hasMember && chatDisplay && (
        <div
          style={{
            position: 'fixed',
            bottom: '30px',
            right: 0,
            width: '500px',
            height: '400px',
            borderTopLeftRadius: '4px',
            boxShadow: 'rgba(0, 0, 0, 0.05) 0px 0px 25px 0px',
            background: '#fff',
          }}
        >
          <Messaging />
        </div>
      )}
    </div>
  );
}

function NetworkEnvironment() {
  let { network } = useSelector((state: StoreState) => {
    return {
      network: state.common.network,
    };
  });
  return (
    <div style={{ textAlign: 'left' }}>
      <div style={{ padding: '10px', borderBottom: 'solid 1pt #ccc' }}>
        <Typography style={{ fontSize: '14px', fontWeight: 'bold' }}>
          {network.label}
        </Typography>
      </div>
      <ul>
        <li>
          <Typography>
            Registry Program: {network.registryProgramId.toString()}
          </Typography>
        </li>
        <li>
          <Typography>
            Stake Pool Program: {network.stakeProgramId.toString()}
          </Typography>
        </li>
        <li>
          <Typography>
            Lockup Program: {network.lockupProgramId.toString()}
          </Typography>
        </li>
        <li>
          <Typography>
            Meta Entity Program: {network.lockupProgramId.toString()}
          </Typography>
        </li>
        <li>
          <Typography>
            Registrar Account: {network.registrar.toString()}
          </Typography>
        </li>
        <li>
          <Typography>Safe Account: {network.safe.toString()}</Typography>
        </li>
        <li>
          <Typography>SRM Mint: {network.srm.toString()}</Typography>
        </li>
        <li>
          <Typography>MSRM Mint: {network.msrm.toString()}</Typography>
        </li>
      </ul>
    </div>
  );
}
